const tl_img = gsap.timeline({ease: "Power1.easeInOut"})
  .to(".men .img-overlay", { // выезжает сверху оверлей
    duration: 1,
    y: "100%"
  })
  .to(".women .img-overlay", { //выезжает сверху оверлей
    duration: 1,
    y: "100%"
  }, "-=.7")  //время задержки 
  .from(".img img", {  // появление 
    opacity: 0,
    duration: 1, // скорость появления из опасити картинки
    scale: 1.2,
    stagger: .2 //задержка между появлением 1 и 2 картинки
  })

  .from(".text h1", { // анимация заголовков
    opacity: 0,
    duration: 1,
    y: 50
  })
  .from(".text h5", {
    opacity: 0,
    duration: 1,
    y: 50
  }, "-=.5")
  
  .to(".content", {    
    y: "75px"
  })

const tl_header = gsap.timeline({ease: "Power1.easeInOut"})
  .from(".header", {  // хедер появляется не сразу после загрузки браузера
    delay: 4.5,
    y: -75
  })

  .from(".header .logo", {  // хедер появляется не сразу после загрузки браузера
    duration: 1,
    y: -75
  })

  .from(".header .menu li", {  // хедер появляется не сразу после загрузки браузера
    duration: 1,
    y: -75,
    stagger: .1
  }, "-=.4")